.jury__wrapper {
  position: relative;
  max-width: 1160px;
  margin: 0 auto;
  padding: 8px 0 120px;

  @include vp-1365 {
    padding-top: 0;
    padding-bottom: 100px;
    margin-top: -3px;
  }

  @include vp-767 {
    padding-bottom: 70px;
    padding-top: 8px;
  }
}

.jury__title {
  margin: 0 0 39px 0;
  font-family: $ff-alumni-sans;
  font-size: 54px;
  font-weight: 600;
  line-height: 1.2;
  letter-spacing: 1.62px;

  @include vp-1365 {
    margin-bottom: 35px;
    text-align: center;
    font-size: 42px;
    letter-spacing: 1.26px;
  }

  @include vp-767 {
    margin-bottom: 38px;
    font-size: 32px;
    letter-spacing: 0.96px;
  }
}

.jury__swiper {
  @include vp-1365 {
    margin: 0 55px;
  }

  @include vp-767 {
    margin: 0 32px;
  }
}

.jury__list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.jury__item {
  position: relative;
  color: $color-default-white;
  background-color: $color-sapphire-89;

  &:hover h3,
  &:focus h3 {
    display: none;
  }

  &:hover .jury-information,
  &:focus .jury-information {
    opacity: 1;
    transform: scale(1);
  }
}

.jury__item img {
  width: 100%;
  height: 100%;
  object-fit: cover;

  @include vp-1365 {
    max-height: 588px;
  }

  @include vp-767 {
    max-height: 548px;
  }
}

.jury__item-title {
  position: absolute;
  margin: 0;
  padding: 0 20px;
  left: 50%;
  transform: translateX(-50%);
  bottom: 20px;
  width: 100%;
  text-align: center;
  max-width: 100%;
  font-size: 22px;
  font-weight: 400;
  line-height: 150%;

  @include vp-1365 {
    font-size: 20px;
    bottom: 22px;
  }

  @include vp-767 {
    font-size: 22px;
    bottom: 20px;
  }
}

.jury__information {
  position: absolute;
  top: 0;
  left: 0;
}

.jury__button {
  position: absolute;
  width: 56px;
  height: 56px;
  border: none;
  background-color: $color-transparent;
  cursor: pointer;

  &:not([disabled]):hover,
  &:not([disabled]):focus {
    border-radius: 50%;
    border: 1px solid $color-sapphire;
  }

  &:not([disabled]):active {
    border-radius: 50%;
    background-color: $color-sapphire;

    & svg {
      color: $color-default-white;
    }
  }

  &[disabled] {
    opacity: 0.3;
    pointer-events: none;
    cursor: default;
  }

  @include vp-1365 {
    top: 42.5%;
  }

  @include vp-767 {
    width: 30px;
    height: 40px;
    top: 47%;

    &:not([disabled]):hover,
    &:not([disabled]):focus {
      border: none;
    }

    &:not([disabled]):active {
      background-color: $color-transparent;

      & svg {
        color: $color-sapphire;
      }
    }
  }

  &--prev {
    top: 44.2%;
    left: -72px;

    & svg {
      left: 24px;
      top: -7px;
    }

    @include vp-1365 {
      top: 42.9%;
      left: -16px;
    }

    @include vp-767 {
      top: 47.5%;
      left: -3px;

      & svg {
        left: 11px;
        top: 0;
      }
    }
  }

  &--next {
    top: 43.7%;
    right: -74px;
    transform: rotate(180deg);

    & svg {
      right: 12px;
      top: -7px;
    }

    @include vp-1365 {
      top: 42.5%;
      right: -18px;
    }

    @include vp-767 {
      right: -2px;
      top: 47.5%;

      & svg {
        right: 10px;
        top: 2px;
      }
    }
  }
}

.jury__button svg {
  color: $color-sapphire;
  position: absolute;
  transform: translate(-50%, 50%);

  @include vp-767 {
    width: 10px;
    height: 19px;
  }
}
